import store from "./store";

export async function search() {
    const url1 = "data/planets/Kepler-22b.json";
    const url2 = "data/planets/Kepler-62f.json";
    fetch(url1)
        .then(res => res.json())
        .then(res => {
            console.log(res.pl_name)
            let news = [];
            news = [...store.getState().item];
            news.push(res)
            store.setState({
                item: news
            });
        })
    fetch(url2)
        .then(res => res.json())
        .then(res => {
            console.log(res.pl_disc)
            let news = [];
            news = [...store.getState().item];
            news.push(res)
            store.setState({
                item: news
            })
        })
}





